<template xmlns:v-on="http://www.w3.org/1999/xhtml">
  <div>
    <div class="login-wrap" v-show="showLogin">
      <h3></h3>
      <p v-show="showMessage">{{Message}}</p>
      <!--ref="form" :model="form"-->
      <el-form>
        <el-form-item>
          <el-input type="text" placeholder="" v-model="username"></el-input>
        </el-form-item>
        <el-form-item>
          <el-input type="password" placeholder="" v-model="password"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="login"></el-button>
        </el-form-item>
      </el-form>
      <span v-on:click="ToRegister"></span>
    </div>

    <div class="register-wrap" v-show="showRegister">
      <h3></h3>
      <p v-show="showMessage">{{Message}}</p>
      <!--ref="form"-- :model="form_register"-->
      <el-form>
        <el-form-item>
          <el-input type="text" placeholder="" v-model="newUsername"></el-input>
        </el-form-item>
        <el-form-item>
          <el-input type="password" placeholder="" v-model="newPassword"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="login"></el-button>
        </el-form-item>
      </el-form>
      <span v-on:click="ToLogin"></span>
    </div>
  </div>
</template>

<style>
  .login-wrap {
    text-align: center;
  }

  .el-input {
    width: 250px;
  }

  p {
    color: red;
  }

  span {
    cursor: pointer;
  }

  span:hover {
    color: #41b883;
  }
</style>

<script>
  import {setCookie, getCookie} from '../assets/js/cookie.js'
  export default{
    data(){
      return {
        showLogin: true,
        showRegister: false,
        showMessage: false,
        Message: '',
        username: '',
        password: '',
        newUsername: '',
        newPassword: ''
      }
    },
    mounted(){
      /*cookieusernamecookie*/
      if (getCookie('username')) {
        this.$router.push('/home')
      }
    },
    methods: {
      login: function () {
        //setTimeout(function () {
          this.$router.push('/home')
       // }.bind(this), 1000)
        /*if (this.username == "" || this.password == "") {
          //this.Message="";
          //this.showMessage=true;
          this.$message('');
        } else {
          let data = {'username': this.username, 'password': this.password}
          /!**!/
          this.$http.post('http://localhost:8080/admin/login.action?', data).then((res)=> {
            console.log(res)
            /!*(-1,),(0,)*!/
            if (res.data == -1) {
              this.Message = ""
              this.showMessage = true
            } else if (res.data == 0) {
              this.Message = ""
              this.showMessage = true
            } else if (res.data == 'admin') {
              /!*this.$router.push*!/
              this.$router.push('/main')
            } else {
              this.Message = ""
              this.Message = true
              setCookie('username', this.username, 1000 * 60)
              setTimeout(function () {
                this.$router.push('/home')
              }.bind(this), 1000)
            }
          })
        }*/
      },
      register: function () {
        if (this.newUsername == "" || this.newPassword == "") {
          alert("")
        } else {
          let data = {'username': this.newUsername, 'password': this.newPassword}
          this.$http.post('http://localhost/vueapi/index.php/Home/user/register', data).then((res)=> {
            console.log(res)
            if (res.data == "ok") {
              this.Message = ""
              this.showMessage = true
              this.username = ''
              this.password = ''
              /**/
              setTimeout(function () {
                this.showRegister = false
                this.showLogin = true
              }.bind(this), 1000)
            }
          })
        }
      },
      ToRegister: function () {
        this.showLogin = false;
        this.showRegister = true;
      },
      ToLogin: function () {
        this.showLogin = true;
        this.showRegister = false;
      }
    }
  }
</script>



