<template>
    <div>
      <div id="project" class="item_container">
        <div id="pro_head" class="item_head">
          <span class="head_line item_head_line_left"></span>
          <span class="item_head_title"></span>
          <span class="head_line item_head_line_right"></span>
          <div style="float: right;margin-right: 20px;">
            <el-button type="text" icon="el-icon-circle-plus-outline" class="item_head_btn"
                       v-on:click="showProjectEditMode(-1)">{{projectEditText}}
            </el-button>
          </div>
        </div>
        <div v-if="projectEditMode">

          <el-form :label-position="top" ref="form" :model="proItemCur" label-width="80px">

            <el-form-item label="">
              <el-input v-model="proItemCur.name"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="proItemCur.position"></el-input>
            </el-form-item>
            <el-form-item label="" style="text-align: start;">
              <el-col :span="11">
                <el-date-picker type="month" placeholder="" v-model="proItemCur.startTime"></el-date-picker>
              </el-col>
              <el-col :span="2" style="opacity: 0;"></el-col>
              <el-col :span="11">
                <el-date-picker type="month" placeholder="" v-model="proItemCur.endTime"></el-date-picker>
              </el-col>
            </el-form-item>
            <el-form-item label="" prop="desc">
              <el-input type="textarea" v-model="proItemCur.duty"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" v-on:click="UpdateProject(proItemCur.id)"></el-button>
              <el-button v-on:click="closeProjectEditMode(proItemCur.id)"></el-button>
            </el-form-item>
          </el-form>
        </div>
        <div id="pro_content" class="item_content">
          <div class="sub_item work_item" v-for=" item in projectData"
               style="overflow: hidden;margin:25px 20px">
            <div class="item_left_content" style="float: left">
              <div class="item_left_top" style="float: left">
                <div class="item_top_content" style="text-align: start;padding: 5px">
                  <div class="item_title" style="font-size: 16px;">{{item.name}}</div>
                  <div class="item_subtitle" style="color: #999;font-size: 14px;">{{item.position}}</div>
                </div>
              </div>

            </div>
            <div class="item_right_content" style="float: right;text-align: end">
              <el-button type="text" icon="el-icon-edit" v-on:click="showProjectEditMode(item.id)"> </el-button>
              <div class="duration_text">{{item.duration}}</div>
            </div>
            <div class="item_left_botton"
                 style="clear: both;color: #555;font-size: 14px;text-align: start">
              {{item.duty}}

            </div>
          </div>


        </div>
      </div>
    </div>
</template>

<script>
    export default{
        data: function () {
            return {
              proItemCur:'',
              proItemNew:{
                id:'',
                name:'',
                position:'',
                duty:'',
                duration:'',
                startTime:'',
                endTime:''
              },
              projectEditMode: false,
              projectEditText: '',
              projectData: [
                {
                  id:'0',
                  name: '',
                  position: 'web',
                  duty: 'excelbootstrapjQuerytable formiframeformexcelwindow.print',
                  duration: '2017.04 - ',
                  startTime:'2017.04',
                  endTime:'2018.03'
                },
                {
                  id:'1',
                  name: '',
                  position: '',
                  duty: ',android textview linearlayout codingsqlite ',
                  duration: '2017.02 - 2017.04',
                  startTime:'2017.02',
                  endTime:'2017.04'
                },
                { id:'2',
                  name: '',
                  position: '',
                  duty: '1.web--- jQuery fullpage bootstrapcss3 2.h5---jQuery mobile Angular JS 3.---  htmlcsscssjscssbannerjQueryAjaxphpphpmysql ajaxphpajaxphpmysqljsontabcanvascanvas',
                  duration: '2016.10 - 2017.02',
                  startTime:'2016.10',
                  endTime:'2017.02'
                },
                { id:'3',
                  name: 'pad',
                  position: '',
                  duty: 'android  ',
                  duration: '2016.02 - 2016.10',
                  startTime:'2016.02',
                  endTime:'2016.10'
                },
                { id:'4',
                  name: 'android os ',
                  position: '',
                  duty: 'androidframework app ',
                  duration: '2014.07 - 2016.02',
                  startTime:'2014.07',
                  endTime:'2016.02'
                }
              ],

            }
        },
        methods: {
          showProjectEditMode: function (id) {
            if(id!=-1){
              this.proItemCur=this.projectData[id];
            }
            this.projectEditMode = !this.projectEditMode;
            this.projectEditText = this.projectEditText == '' ? '' : '';
          },
          UpdateProject: function () {
            this.projectEditMode = false;
            this.projectEditText = '';
            this.proItemCur=this.proItemNew;
          },
          closeProjectEditMode:function () {
            this.projectEditMode = false;
            this.projectEditText = '';
            this.proItemCur=this.proItemNew;
          }
        },
        mounted(){
          this.proItemCur=this.proItemNew;
        }
    }
</script>



