<template>
  <div class="app-nav-wrap">
    <el-menu :default-active="$route.path"  router>
      <el-menu-item
        v-for="menu in menus"
        :index="menu.route"
        :key="menu.route">
        <i class="el-icon-menu"></i>{{menu.name}}
      </el-menu-item>
    </el-menu>
  </div>
</template>

<script>
    export default{

        data: function () {
            return {
              menus: [
                { route: '/', name: ''},
                { route: '/work', name: 'work/'},
                { route: '/study', name: 'study/'},
                { route: '/health', name: 'health/'},
                { route: '/finance', name: 'finance/'},
                { route: '/travel', name: 'travel/'},
                { route: '/life', name: 'life/'},
                { route: '/aboutme', name: 'About Me/'},
                { route: '/update', name: 'Update Log/'},
                { route: '/other', name: 'other/'}
              ]
            }
        },
        methods: {},
        mounted(){
        }
    }
</script>



